$primary-color: #047aed;

.showcase {
  height: 400px;
  background-color: $primary-color;
  color: #fff;
  position: relative;
  h1 {
    font-size: 40px;    
  }
  p {
    margin: 20px 0;
  }
  .grid {
    overflow: visible;
    grid-template-columns: 55% 45%;
    gap: 30px;
  }
}

.showcase-form {
  position: relative;
  top: 60px;
  height: 350px;
  width: 400px;
  z-index: 100;
  justify-self: flex-end;
  .form-control {
    margin: 30px 0;
  }
  input[type='text'],
  input[type='email']
   {
    border: 0;
    border-bottom: 1px solid #b4becb;
    width: 100%;
    padding: 3px;
    font-size: 16px;
    &:focus {
      outline: none;
    }
  }
}

.showcase::before,
.showcase::after {
  content: '';
  position: absolute;
  height: 100px;
  bottom: -70px;
  right: 0;
  left: 0;
  background: #fff;
  transform: skewY(-3deg);
  -webkit-transform: skewY(-3deg);
  -moz-transform: skewY(-3deg);
  -ms-transform: skewY(-3deg);
}
