@import url('https://fonts.googleapis.com/css2?family=Lato:wght@300&display=swap');

// $primary-color: #047aed;
$primary-color: #085458;
$secondary-color: #1c3fa8;
$dark-color: #002240;
$light-color: #f4f4f4;

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: 'Lato', sans-serif;
  color: #333;
  line-height: 1.6;
}

ul{
  list-style-type: none;
}

a {
  text-decoration: none;
  color: #333;
}

h1, h2 {
  font-weight: 300;
  line-height: 1.2;
  margin: 10px 0;
}

p{
  margin: 10px 0;
}

img {
  width: 100%
}

.text-center {
  text-align: center;
}


.container {
  max-width: 1100px;
  margin: 0 auto;
  overflow: auto;
  padding: 0 40px;
}

.flex {
  display: flex;
  justify-content: center;
  justify-items: center;
  height: 100%;
}

.grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 20px;
  justify-content: center;
  align-items: center;
  height: 100%;
}
.grid-3 {
  grid-template-columns: repeat(3, 1fr);
}
.card {
  background-color: #fff;
  color: #333;
  border-radius: 10px;
  box-shadow: 0 3px 10px rgba(0, 0, 0, 0.2);
  padding: 20px;
}

.btn {
  display: inline-block;
  padding: 10px 30px;
  background: $primary-color;
  color: #fff;
  border:none;
  border-radius: 5px;
}

.btn-outline {
  background-color: transparent;
  border: 1px #fff solid;
}

.btn:hover {
  transform: scale(.98);
}

// background & colored buttons
.bg-primary,
.btn-primary {
  background-color: $primary-color;
  color: #fff;
}

.bg-secondary,
.btn-secondary {
  background-color: $secondary-color;
  color: #fff;
}

.bg-dark,
.btn-dark {
  background-color: $dark-color;
  color: #fff;
}

.bg-light,
.btn-light {
  background-color: $light-color;
  color: #fff;
}

// Text sizes 
.lead {
  font-size: 20px;
}

.sm {
  font-size: 1rem;
}

.md {
  font-size: 2rem;
}

.lg {
  font-size: 3rem;
}

.xl {
  font-size: 4rem;
}




